:root {
  --not-quite-black: #23272a;
  --black: #000000;
  --white: #fff;
  --blurple: #7289da;
  --header-color: #151617;
  --text-muted: #6c757d;
}

.header-nav-divider {
  width: 1px;
  height: 36px;
  margin-top: 16px;
  margin-bottom: 10px;
  background-color: white;
  opacity: 0.2;
  margin-right: 20px;
  margin-left: 7px;
}

@media screen and (max-width: 575px) {
  .header-nav-divider {
    visibility: hidden;
  }

  /* .navbar-brand {
    left: 70px;
    position: relative;
  } */
}

.footer-wrap {
  font-size: large;
}

header {
  transition: transform 2s cubic-bezier(0.65, 0, 0.35, 1);;
}

.bg-nqb {
  background-color: var(--not-quite-black);
}

.App {
  background-color: var(--bg-color);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  color: #fff;
  text-align: center;
}

.Header-1A {
  background-color: #151617;
  background: var(--header-color);
}

.Header-1A::after {
  content: " ";
  height: 1px;
  margin-top: 16px;
  background-color: white;
  opacity: 0.2;
  margin-right: 20px;
  margin-left: 7px;
  display: block;
}

/* footer ul, footer li, footer ul a, footer li a, footer ul a:visited, footer li a:visited {
  color: var(--text-muted) !important;
} */

.header-brand-1A {
  font-weight: bold;
  letter-spacing: 3.5px;
  margin-left: 5px;
  font-size: 2rem;
  vertical-align: middle;
  text-transform: uppercase;
}

.navbar-brand {
  color: white !important;
  color: #fff !important;
  color: var(--white) !important;
}

